<template>
  <v-container id="profile-container">
    <ValidationObserver ref="observer">
      <form autocomplete="false" @submit.prevent="updateProfile">
        <v-container style="padding: 12px 4rem;">

          <h2 class="text-bolder my-6 mx-0">Datos de la cuenta</h2>

            <v-row justify="space-between">
              <v-col >
                <ValidationProvider
                    v-slot="{ errors }"
                    name="name"
                    rules="required"
                  >
                  <label for="name">Nombre</label>
                  <v-text-field
                    id="name"
                    v-model="profile.name"
                    placeholder="Nombre completo"
                    outlined
                    dense
                    class="mx-auto rounded-lg"
                    :error-messages="errors"
                    :hide-details="errors.length > 0 ? false : true"
                  ></v-text-field>
                </ValidationProvider>
              </v-col>
              <v-col>
                <ValidationProvider
                    v-slot="{ errors }"
                    name="lastname"
                    rules="required"
                  >
                  <label for="name">Apellidos</label>
                <v-text-field
                  id="lastname"
                  v-model="profile.lastname"
                  placeholder="Apellidos completo"
                  outlined
                  dense
                  class="mx-auto rounded-lg"
                  :error-messages="errors"
                  :hide-details="errors.length > 0 ? false : true"
                ></v-text-field>
              </ValidationProvider>
              </v-col>
            </v-row>

            <v-row justify="space-between">
              <v-col>
                <ValidationProvider
                    v-slot="{ errors }"
                    name="email"
                    rules="required"
                  >
                  <label for="email">Correo electrónio</label>
                  <v-text-field
                    id="email"
                    v-model="profile.email"
                    placeholder="Escriba su correo"
                    outlined
                    dense
                    class="mx-auto rounded-lg"
                    :error-messages="errors"
                    :hide-details="errors.length > 0 ? false : true"
                  ></v-text-field>
                </ValidationProvider>
              </v-col>
              <v-col>
                <ValidationProvider
                    v-slot="{ errors }"
                    name="cell_phone"
                  >
                  <label for="cell_phone">Teléfono móvil</label>
                <v-text-field
                  id="cell_phone"
                  v-model="profile.cell_phone"
                  placeholder="Escriba su número"
                  outlined
                  dense
                  class="mx-auto rounded-lg"
                  :error-messages="errors"
                  :hide-details="errors.length > 0 ? false : true"
                ></v-text-field>
              </ValidationProvider>
              </v-col>
            </v-row>

            <v-row justify="space-between">
              <v-col>
                <ValidationProvider
                    v-slot="{ errors }"
                    name="address1"
                    rules="required"
                  >
                  <label for="address1">Dirección (Linea 1)</label>
                  <v-text-field
                    id="address1"
                    v-model="profile.address1"
                    placeholder="Dirección exacta"
                    outlined
                    dense
                    class="mx-auto rounded-lg"
                    :error-messages="errors"
                    :hide-details="errors.length > 0 ? false : true"
                  ></v-text-field>
                </ValidationProvider>
              </v-col>
              <v-col>
                <ValidationProvider
                    v-slot="{ errors }"
                    name="address2"
                  >
                  <label for="address2">Dirección (Linea 2)</label>
                <v-text-field
                  id="address2"
                  v-model="profile.address2"
                  placeholder="Dirección exacta"
                  outlined
                  dense
                  class="mx-auto rounded-lg"
                  :error-messages="errors"
                  :hide-details="errors.length > 0 ? false : true"
                ></v-text-field>
              </ValidationProvider>
              </v-col>
            </v-row>

            <v-row justify="space-between">
              <v-col>
                <ValidationProvider
                    v-slot="{ errors }"
                    name="city"
                    rules="required"
                  >
                  <label for="city">Ciudad</label>
                  <v-select
                    id="city"
                    v-model="profile.city"
                    placeholder="Seleccionar ciudad"
                    outlined
                    dense
                    class="mx-auto rounded-lg"
                    no-data-text="No hay datos"
                    :error-messages="errors"
                    :hide-details="errors.length > 0 ? false : true"
                  ></v-select>
                </ValidationProvider>
              </v-col>
              <v-col>
                <ValidationProvider
                  v-slot="{ errors }"
                  name="state"
                  rules="required"
                >
                <label for="state">Estado</label>
                <v-select
                  id="state"
                  v-model="profile.state"
                  placeholder="Seleccionar estado"
                  outlined
                  dense
                  class="mx-auto rounded-lg"
                  no-data-text="No hay datos"
                  :error-messages="errors"
                  :hide-details="errors.length > 0 ? false : true"
                ></v-select>
              </ValidationProvider>
              </v-col>
            </v-row>

            <v-row justify="space-between">
              <v-col>
                <ValidationProvider
                  v-slot="{ errors }"
                  name="postal_code"
                >
                  <label for="postal_code">Código postal</label>
                  <v-text-field
                    id="postal_code"
                    v-model="profile.postal_code"
                    placeholder="Código Zip"
                    outlined
                    dense
                    class="mx-auto rounded-lg"
                    :error-messages="errors"
                    :hide-details="errors.length > 0 ? false : true"
                  ></v-text-field>
                </ValidationProvider>
              </v-col>
              <v-col>
                <v-menu
                    ref="date_menu"
                    v-model="date_menu"
                    :close-on-content-click="false"
                    transition="scale-transition"
                    offset-y
                    max-width="290px"
                    min-width="auto"
                  >
                    <template v-slot:activator="{ on, attrs }">
                      <ValidationProvider
                        v-slot="{ errors }"
                        name="birth_date"
                        rules="required"
                      >
                      <label for="birth_date">Fecha de nacimiento</label>
                        <v-text-field
                          id="birth_date"
                          v-model="profile.birth_date"
                          placeholder="Seleccionar fecha"
                          color="primary"
                          persistent-hint
                          append-inner-icon="mdi-calendar"
                          v-bind="attrs"
                          @blur="date = parseDate(profile.birth_date)"
                          v-on="on"
                          class="mx-auto rounded-lg"
                          :error-messages="errors"
                          :hide-details="errors.length > 0 ? false : true"
                          clearable
                          dense
                          outlined
                        ></v-text-field>
                      </ValidationProvider>
                    </template>
                    <v-date-picker
                      v-model="date"
                      no-title
                      color="primary"
                      @input="date_menu = false"
                    ></v-date-picker>
                </v-menu>
              </v-col>
            </v-row>

            <div class="mt-4 pa-3">
              <label class="mt-10">GÉNERO</label>
              <v-radio-group v-model="profile.gender" row>
                <v-radio class="mx-2 mt-0" label="Femenino" value="F"></v-radio>
                <v-radio class="mx-2 mt-0" label="Masculino" value="M"></v-radio>
              </v-radio-group>
            </div>

            <label class="pa-3">Cambiar contraseña</label>
            <v-row justify="space-between">
              <v-col>
                <ValidationProvider
                    v-slot="{ errors }"
                    name="password"
                    rules="required"
                  >
                <v-text-field
                  id="password"
                  v-model="profile.password"
                  type="password"
                  placeholder="Nueva contraseña"
                  outlined
                  dense
                  class="mx-auto rounded-lg"
                  :error-messages="errors"
                  :hide-details="errors.length > 0 ? false : true"
                ></v-text-field>
              </ValidationProvider>
              </v-col>
              <v-col>
                <ValidationProvider
                    v-slot="{ errors }"
                    name="password_confirm"
                    :rules="profile.password != null && profile.password != '' ? 'required' : ''"
                  >
                <v-text-field
                  id="password_confirm"
                  v-model="profile.password_confirm"
                  type="password"
                  placeholder="Confirmar nueva contraseña"
                  outlined
                  dense
                  class="mx-auto rounded-lg"
                  :error-messages="errors"
                  :hide-details="errors.length > 0 ? false : true"
                ></v-text-field>
              </ValidationProvider>
              </v-col>
            </v-row>

            <v-row justify="space-between" class="px-3">
              <v-checkbox
                v-model="profile.accept_notifications"
                label="Acepto recibir noticiones y promociones de CUBAMAX"
              ></v-checkbox>
            </v-row>

            <v-row style="margin: 5rem 0 3rem !important" justify="center">
              <v-btn type="submit" class="mx-2" rounded elevation="0" color="primary">Actualizar</v-btn>
            </v-row>
        </v-container>
      </form>
    </ValidationObserver>
  </v-container>
</template>

<script>
export default {
  name: 'Profile',
  data: function () {
    return {
      date: null,
      date_menu: null,
      profile: {
        // name: null,
        // lastname: null,
        // phone: null,
        // id: null,
        // address1: null,
        // address2: null,
        // city: null,
        // state: null,
        // alternative_destinatary: null,
        // birth_date: null,
        // deny_alternative_destinatary: false
        name: 'Alfredo Suarez',
        lastname: 'Garrido',
        email: 'cubamax@gmail.com',
        cell_phone: '+1 568 5546 568',
        id: '89565123165',
        address1: 'Cuba',
        address2: null,
        city: 'La habana',
        state: 'Playa',
        postal_code: '',
        phone: null,
        birth_date: null,
        gender: 'M',
        password: null,
        password_confirm: null,
        accept_notifications: false
      }
    }
  },
  watch: {
    date (val) {
      this.profile.birth_date = this.formatDate(this.date)
    }
  },
  methods: {
    formatDate (date) {
      if (!date) return null

      const [year, month, day] = date.split('-')
      return `${day}/${month}/${year}`
    },
    parseDate (date) {
      if (!date) return null

      const [day, month, year] = date.split('/')
      return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
    },
    updateProfile () {
      this.$refs.observer.validate().then((result) => {
        if (result) {
          // const alternative = !this.profile.deny_alternative_destinatary ? '   (Destinatario alternativo: ' + this.profile.alternative_destinatary + ')' : ''
          // const newDestinatary = this.profile.name + ' ' + this.profile.lastname + alternative
          // console.log('alternative', alternative)
          // console.log('newDestinatary', newDestinatary)
          // this.$emit('newDestinatarySaved', newDestinatary)
          alert('Usuario actualizado!')
        }
      })
    },
    cancel () {
      this.$emit('cancel')
    }
  }
}
</script>

<style lang="scss" scoped>
.row{
  margin: 7px 0 !important;
}
h3{
  text-transform: uppercase;
}

</style>
