<template>
  <v-container class="mt-10">
    <ValidationObserver ref="observer">
      <form autocomplete="off">
        <FileUpload />

        <!-- Nombre y telefono -->
        <v-row justify="space-between">
          <v-col md="6" sm="12" xs="12">
            <ValidationProvider
              v-slot="{ errors }"
              name="name"
              rules="required"
            >
              <label for="name">Nombre</label>
              <v-text-field
                ref="name"
                id="name"
                v-model="editedItem.name"
                placeholder="Nombre del restaurante"
                outlined
                dense
                class="mx-auto rounded-lg"
                :error-messages="errors"
                :hide-details="errors.length > 0 ? false : true"
              >
              </v-text-field>
            </ValidationProvider>
          </v-col>
          <v-col md="6" sm="12" xs="12">
            <ValidationProvider
              v-slot="{ errors }"
              name="phone"
              rules="required"
            >
              <label for="phone">Teléfono</label>
              <v-text-field
                ref="phone"
                id="phone"
                v-model="editedItem.phone"
                placeholder="Teléfono del restaurante"
                outlined
                dense
                class="mx-auto rounded-lg"
                :error-messages="errors"
                :hide-details="errors.length > 0 ? false : true"
              >
              </v-text-field>
            </ValidationProvider>
          </v-col>
        </v-row>

        <!-- Correo y Web -->
        <v-row justify="space-between">
          <v-col md="6" sm="12" xs="12">
            <ValidationProvider
              v-slot="{ errors }"
              name="email"
              rules="required|email"
            >
              <label for="email">Correo electrónico</label>
              <v-text-field
                ref="email"
                id="email"
                v-model="editedItem.email"
                placeholder="Escriba el correo"
                outlined
                dense
                class="mx-auto rounded-lg"
                :error-messages="errors"
                :hide-details="errors.length > 0 ? false : true"
              >
              </v-text-field>
            </ValidationProvider>
          </v-col>
          <v-col md="6" sm="12" xs="12">
            <ValidationProvider
              v-slot="{ errors }"
              name="web"
              rules="required"
            >
              <label for="web">Página web</label>
              <v-text-field
                ref="web"
                id="web"
                v-model="editedItem.web"
                placeholder="www://cubamax.com"
                outlined
                dense
                class="mx-auto rounded-lg"
                :error-messages="errors"
                :hide-details="errors.length > 0 ? false : true"
              >
              </v-text-field>
            </ValidationProvider>
          </v-col>
        </v-row>

        <!-- Direccion -->
        <v-row justify="space-between">
          <v-col md="6" sm="12" xs="12">
            <ValidationProvider
                v-slot="{ errors }"
                name="address1"
                rules="required"
              >
              <label for="address1">Dirección (Linea 1)</label>
            <v-text-field
              ref="address1"
              id="address1"
              v-model="editedItem.address1"
              placeholder="Dirección exacta"
              outlined
              dense
              class="mx-auto rounded-lg"
              :error-messages="errors"
              :hide-details="errors.length > 0 ? false : true"
            ></v-text-field>
          </ValidationProvider>
          </v-col>
          <v-col md="6" sm="12" xs="12">
            <ValidationProvider
                v-slot="{ errors }"
                name="address2"
              >
              <label for="address2">Dirección (Linea 2)</label>
            <v-text-field
              ref="address2"
              id="address2"
              v-model="editedItem.address2"
              placeholder="Dirección exacta"
              outlined
              dense
              class="mx-auto rounded-lg"
              :error-messages="errors"
              :hide-details="errors.length > 0 ? false : true"
            ></v-text-field>
          </ValidationProvider>
          </v-col>
        </v-row>

        <!-- Provincia y Municipio -->
        <v-row justify="space-between">
          <v-col md="6" sm="12" xs="12">
            <ValidationProvider
              v-slot="{ errors }"
              name="province"
              rules="required"
            >
              <label for="province">Provincia</label>
              <v-select
                ref="province"
                id="province"
                v-model="editedItem.province"
                :items="provinces"
                placeholder="Selecionar provincia"
                outlined
                dense
                class="mx-auto rounded-lg"
                no-data-text="No hay datos"
                :error-messages="errors"
                :hide-details="errors.length > 0 ? false : true"
              ></v-select>
            </ValidationProvider>
          </v-col>
          <v-col md="6" sm="12" xs="12">
            <ValidationProvider
              v-slot="{ errors }"
              name="municipality"
              rules="required"
            >
              <label for="municipality">Municipio</label>
              <v-select
                ref="municipality"
                id="municipality"
                v-model="editedItem.municipality"
                :items="municipalities"
                placeholder="Seleccionar municipio"
                outlined
                dense
                class="mx-auto rounded-lg"
                no-data-text="No hay datos"
                :error-messages="errors"
                :hide-details="errors.length > 0 ? false : true"
              ></v-select>
            </ValidationProvider>
          </v-col>
        </v-row>

        <!-- Tipo de cocina y Rango de precio -->
        <v-row justify="space-between">
          <v-col md="6" sm="12" xs="12">
            <ValidationProvider
              v-slot="{ errors }"
              name="food_type"
              rules="required"
            >
              <label for="food_type">Tipo de cocina</label>
              <v-text-field
                ref="food_type"
                id="food_type"
                v-model="editedItem.food_type"
                placeholder="Tipo de comida"
                outlined
                dense
                class="mx-auto rounded-lg"
                :error-messages="errors"
                :hide-details="errors.length > 0 ? false : true"
              >
              </v-text-field>
            </ValidationProvider>
          </v-col>
          <v-col md="6" sm="12" xs="12">
            <ValidationProvider
              v-slot="{ errors }"
              name="price_range"
              rules="required"
            >
              <label for="price_range">Rango de precios</label>
              <v-select
                ref="price_range"
                id="price_range"
                v-model="editedItem.price_range"
                :items="price_ranges"
                placeholder="Seleccionar rango"
                outlined
                dense
                class="mx-auto rounded-lg"
                :error-messages="errors"
                :hide-details="errors.length > 0 ? false : true"
              >
              </v-select>
            </ValidationProvider>
          </v-col>
        </v-row>

        <!-- Hora de apertura y cierre -->
        <v-row>
          <v-col md="3" sm="6" xs="6">
            <v-menu
                ref="open_date_menu"
                v-model="open_date_menu"
                :close-on-content-click="false"
                :nudge-right="40"
                :return-value.sync="editedItem.open_hour"
                transition="scale-transition"
                offset-y
                max-width="290px"
                min-width="auto"
              >
                <template v-slot:activator="{ on, attrs }">
                  <ValidationProvider
                    v-slot="{ errors }"
                    ref="open_hour"
                    rules="required"
                  >
                    <label for="open_hour">Hora de apertura</label>
                    <v-text-field
                      id="open_hour"
                      v-model="editedItem.open_hour"
                      placeholder="Seleccionar hora"
                      color="primary"
                      persistent-hint
                      prepend-inner-icon="mdi-clock-time-four-outline"
                      v-bind="attrs"
                      v-on="on"
                      class="mx-auto rounded-lg"
                      :error-messages="errors"
                      :hide-details="errors.length > 0 ? false : true"
                      clearable
                      dense
                      outlined
                    ></v-text-field>
                  </ValidationProvider>
                </template>
                <v-time-picker
                  v-if="open_date_menu"
                  v-model="editedItem.open_hour"
                  ampm-in-title
                  format="ampm"
                  @click:minute="$refs.open_date_menu.save(editedItem.open_hour)"
                ></v-time-picker>
            </v-menu>
          </v-col>
          <v-col md="3" sm="6" xs="6">
            <v-menu
                ref="closed_date_menu"
                v-model="closed_date_menu"
                :close-on-content-click="false"
                :nudge-right="40"
                :return-value.sync="editedItem.open_hour"
                transition="scale-transition"
                offset-y
                max-width="290px"
                min-width="auto"
              >
                <template v-slot:activator="{ on, attrs }">
                  <ValidationProvider
                    v-slot="{ errors }"
                    ref="closed_hour"
                    rules="required"
                  >
                    <label for="closed_hour">Hora de apertura</label>
                    <v-text-field
                      id="closed_hour"
                      v-model="editedItem.closed_hour"
                      placeholder="Seleccionar hora"
                      color="primary"
                      persistent-hint
                      prepend-inner-icon="mdi-clock-time-four-outline"
                      v-bind="attrs"
                      v-on="on"
                      class="mx-auto rounded-lg"
                      :error-messages="errors"
                      :hide-details="errors.length > 0 ? false : true"
                      clearable
                      dense
                      outlined
                    ></v-text-field>
                  </ValidationProvider>
                </template>
                <v-time-picker
                  v-if="closed_date_menu"
                  v-model="editedItem.closed_hour"
                  ampm-in-title
                  format="ampm"
                  @click:minute="$refs.closed_date_menu.save(editedItem.closed_hour)"
                ></v-time-picker>
            </v-menu>
          </v-col>
        </v-row>

        <!-- Descripcion -->
        <v-row>
          <v-col cols="12">
            <label for="description">Descripción</label>
            <v-textarea
              ref="description"
              id="description"
              name="description"
              v-model="editedItem.description"
              placeholder="Breve descripcion del restaurante"
              outlined
              hide-details
              class="rounded-lg"
            ></v-textarea>
          </v-col>
        </v-row>

        <!-- Entrega a domicilio -->
        <v-row class="mx-1">
          <v-checkbox
            id="home_delivery"
            v-model="editedItem.home_delivery"
            label="Realizan entregas a domicilio"
          ></v-checkbox>
        </v-row>

        <!-- Informacion de entrega a domicilio -->
        <v-row justify="space-between">
          <v-col md="4" sm="5" xs="12">
            <label for="delivery_time">Tiempo de entrega</label>
            <v-row>
              <v-col class="pr-1" sm="3" xs="3">
                <ValidationProvider v-slot="{ errors }" ref="delivery_time" :rules="editedItem.home_delivery ? 'required|moreThanZero' : ''">
                  <v-text-field
                    id="delivery_time"
                    v-model="editedItem.delivery_time"
                    type="number"
                    outlined
                    dense
                    :error-messages="errors"
                  >
                  </v-text-field>
                </ValidationProvider>
              </v-col>
              <v-col class="pl-1" sm="8" xs="8">
                <ValidationProvider v-slot="{ errors }" ref="delivery_time_unit" :rules="editedItem.home_delivery ? 'required' : ''">
                  <v-select
                    id="delivery_time_unit"
                    v-model="editedItem.delivery_time_unit"
                    :items="['Minutos', 'Horas', 'Días', 'Meses']"
                    placeholder="Seleccionar"
                    outlined
                    dense
                    value="Minutos"
                    no-data-text="No hay datos"
                    :error-messages="errors"
                  >
                  </v-select>
                </ValidationProvider>
              </v-col>
            </v-row>
          </v-col>
          <v-col md="4" sm="5" xs="12">
            <label for="delivery_max_time">Tiempo máximo</label>
            <v-row>
              <v-col class="pr-1" sm="3" xs="3">
                <ValidationProvider v-slot="{ errors }" ref="delivery_max_time" :rules="editedItem.home_delivery ? 'required|moreThanZero' : ''">
                  <v-text-field
                    id="delivery_max_time"
                    v-model="editedItem.delivery_max_time"
                    type="number"
                    outlined
                    dense
                    :error-messages="errors"
                  >
                  </v-text-field>
                </ValidationProvider>
              </v-col>
              <v-col class="pl-1" sm="8" xs="8">
                <ValidationProvider v-slot="{ errors }" ref="delivery_max_time_unit" :rules="editedItem.home_delivery ? 'required' : ''">
                  <v-select
                    id="delivery_max_time_unit"
                    v-model="editedItem.delivery_max_time_unit"
                    :items="['Minutos', 'Horas', 'Días', 'Meses']"
                    placeholder="Seleccionar"
                    outlined
                    dense
                    value="Minutos"
                    no-data-text="No hay datos"
                    :error-messages="errors"
                  >
                  </v-select>
                </ValidationProvider>
              </v-col>
            </v-row>
          </v-col>
          <v-col md="3" sm="2" xs="12">
            <ValidationProvider v-slot="{ errors }" ref="delivery_distance_limit" :rules="editedItem.home_delivery ? 'required|moreThanZero' : ''">
              <label for="delivery_distance_limit">Límite de distancia</label>
              <v-text-field
                id="delivery_distance_limit"
                v-model="editedItem.delivery_distance_limit"
                type="number"
                outlined
                dense
                suffix="Km"
                :error-messages="errors"
              >
              </v-text-field>
            </ValidationProvider>
          </v-col>
        </v-row>

        <v-row class="my-15" justify="center">
          <v-btn type="button" class="mx-2" rounded elevation="0" color="error" @click="close">Cancelar</v-btn>
          <v-btn type="button" class="mx-2" rounded elevation="0" color="primary" @click="save">Guardar</v-btn>
        </v-row>
      </form>
    </ValidationObserver>
  </v-container>
</template>

<script>
import { dateRules } from '@/mixins/date_rules'
import { numberRules } from '@/mixins/number_rules'
import { generalRules } from '@/mixins/general_rules'
import FileUpload from '@/components/image-upload/FileUpload'
// import axiosCustom from '@/axios'
import restaurantService from '@/axios/restaurant.services.js'

export default {
  name: 'CreateRestaurant',
  components: {
    FileUpload
  },
  props: {
    editedItem: {
      type: Object,
      required: true
    }
  },
  data () {
    return {
      open_hour: null,
      closed_hour: null,
      open_date_menu: false,
      closed_date_menu: false,
      restaurant: {
        id: null,
        name: null,
        description: null,
        address1: null,
        address2: null,
        province: null,
        municipality: null,
        phone: null,
        email: null,
        web: null,
        food_type: null,
        price_range: null,
        home_delivery: null,
        delivery_time: null,
        delivery_time_unit: null,
        delivery_max_time: null,
        delivery_max_time_unit: null,
        delivery_distance_limit: null,
        images: []
      },
      provinces: [
        'La Habana'
      ],
      municipalities: [
        'Playa',
        'Plaza',
        'Marianao'
      ],
      price_ranges: [
        '5 - 25',
        '50 - 125',
        '200 - 425',
        '650 - 1250'
      ]
    }
  },
  mixins: [numberRules, dateRules, generalRules],
  created () {
    // axiosCustom.post('/api/service/restaurant', { name: 'New Restaurant' })
    restaurantService.addRestaurant({
      name: 'Cuba',
      slug: 'id',
      email: 'email@mai.com',
      webSite: 'www.com',
      phone: '5487521',
      pricesRange: '1-7',
      deleveryHome: true,
      deleveryTime: 'String',
      limitDelevery: 'String',
      foodType: 'Del Mar',
      startTime: '08:30',
      endTime: '16:00',
      description: 'asd',
      owner: '607baffde6cbd10d54433240',
      address: {
        houseNumber: 3005,
        streetDirection: '',
        streetName: 'Cranberry Hwy Rt 6 28',
        streetSuffix: '',
        city: 'Wareham',
        state: 'MA',
        zip: '2538'
      },
      image: '123'
    })
    // restaurantService.getRestaurantById({ id: '6080781f6a29bd78dcafab26' })
  },
  watch: {
    // open_hour (val) {
    //   this.editedItem.open_hour = this.formatDate(this.open_hour)
    // },
    // closed_hour (val) {
    //   this.editedItem.closed_hour = this.formatDate(this.closed_hour)
    // }
  },
  methods: {
    close () {
      for (const key in this.$refs) {
        if (key !== 'observer' && key !== 'open_date_menu' && key !== 'closed_date_menu') {
          this.$refs[key].reset()
        }
      }
      this.$emit('close')
    },

    save () {
      this.$refs.observer.validate().then((result) => {
        if (result) {
          this.$emit('save')
        }
      })
    },

    formatDate (date) {
      if (!date) return null

      const [year, month, day] = date.split('-')
      return `${day}/${month}/${year}`
    },

    parseDate (date) {
      if (!date) return null

      const [day, month, year] = date.split('/')
      return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
    }
  }
}
</script>

