<template>
  <v-dialog v-model="dialog" max-width="500px" scrollable>
    <ValidationObserver ref="observer">
      <form ref="myform">
        <v-card>
          <v-card-title>
            <span class="headline">ASD</span>
          </v-card-title>

          <v-card-text>
            <v-container>
              <v-row>
                <v-col
                  cols="12"
                  sm="12"
                  md="12"
                >
                  <ValidationProvider ref="name" v-slot={errors} rules="required">
                    <v-text-field
                      id="name"
                      v-model="editedItem.name"
                      label="Nombre en la tarjeta"
                      rounded
                      outlined
                      dense
                      :error-messages="errors"
                      :hide-details="errors.length ? false : true"
                    ></v-text-field>
                  </ValidationProvider>
                </v-col>
                <v-col
                  cols="12"
                  sm="12"
                  md="12"
                >
                <ValidationProvider ref="number" v-slot={errors} rules="required|positive|max:12">
                    <v-text-field
                      id="number"
                      v-model="editedItem.number"
                      label="Número de tarjeta"
                      rounded
                      outlined
                      dense
                      :error-messages="errors"
                      :hide-details="errors.length ? false : true"
                    ></v-text-field>
                  </ValidationProvider>
                </v-col>
                <v-col
                  cols="12"
                  sm="12"
                  md="12"
                >
                  <v-menu
                      ref="menu"
                      v-model="menu"
                      :close-on-content-click="false"
                      transition="scale-transition"
                      offset-y
                      max-width="290px"
                      min-width="auto"
                    >
                      <template v-slot:activator="{ on, attrs }">
                        <ValidationProvider ref="dateFrom" v-slot={errors} rules="required">
                          <v-text-field
                            id="dateFrom"
                            v-model="editedItem.expiration_date"
                            label="Fecha de expiración"
                            :rules="[is_date]"
                            color="primary"
                            append-icon="mdi-calendar"
                            @blur="date = parseDate(editedItem.expiration_date)"
                            v-bind="attrs"
                            v-on="on"
                            clearable
                            dense
                            rounded
                            outlined
                            :error-messages="errors"
                            :hide-details="errors.length ? false : true"
                          ></v-text-field>
                        </ValidationProvider>
                      </template>
                      <v-date-picker
                        v-model="date"
                        no-title
                        color="primary"
                        @input="menu = false"
                      ></v-date-picker>
                  </v-menu>
                </v-col>
                <v-col
                  cols="12"
                  sm="12"
                  md="12"
                >
                  <v-checkbox
                    v-model="editedItem.predetermined_card"
                    label="Hacer predeterminada?"
                  ></v-checkbox>
                </v-col>
              </v-row>
            </v-container>
          </v-card-text>

          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
              color="blue darken-1"
              text
              @click="close"
            >
              Cancelar
            </v-btn>
            <v-btn
              color="blue darken-1"
              text
              @click="save"
            >
              Gaurdar
            </v-btn>
          </v-card-actions>
        </v-card>
      </form>
    </ValidationObserver>
  </v-dialog>
</template>

<script>
import { dateRules } from '@/mixins/date_rules'
import { numberRules } from '@/mixins/number_rules'
import { generalRules } from '@/mixins/general_rules'

// extend('required', {
//   validate (value) {
//     return {
//       required: true,
//       valid: ['', null, undefined].indexOf(value) === -1
//     }
//   },
//   message: 'Rellene este campo.',
//   computesRequired: true
// })

export default {
  name: 'CardCreateDialog',
  props: {
    editedItem: {
      type: Object,
      required: true
    },
    dialog: {
      type: Boolean,
      required: true
    }
  },
  mixins: [numberRules, dateRules, generalRules],
  // data: function () {
  //   return {
  //     card: {
  //       name: null,
  //       number: null,
  //       expiration_date: null,
  //       predetermined_card: null
  //     }
  //   }
  // },
  watch: {
    date (val) {
      this.editedItem.expiration_date = this.formatDate(this.date)
    },
    dialog (val) {
      val || this.close()
    },
    dialogDelete (val) {
      val || this.closeDelete()
    }
  },
  methods: {
    close () {
      for (const key in this.$refs) {
        if (key !== 'observer' && key !== 'menu') {
          this.$refs[key].reset()
        }
      }
      this.$emit('close')
    },

    save () {
      this.$emit('save', this.editItem)
    }
  }
}
</script>

