<template>
  <v-main>
    <v-container>
      <v-row align="center" justify="space-between">
        <v-col cols="9"><h1 class="text-bolder">Información de pago</h1></v-col>
        <v-col cols="3">
          <v-row align="center" justify="space-between">
            <v-img width="64" max-width="64" height="40" max-height="40" src="../../../assets/pay-order/Visa@2x.png" lazy-src="../../../assets/pay-order/Visa@2x.png"></v-img>
            <v-img width="64" max-width="64" height="40" max-height="40" src="../../../assets/pay-order/MasterCard@2x.png" lazy-src="../../../assets/pay-order/Visa@2x.png"></v-img>
            <v-img width="64" max-width="64" height="40" max-height="40" src="../../../assets/pay-order/American_Express@2x.png" lazy-src="../../../assets/pay-order/Visa@2x.png"></v-img>
          </v-row>
        </v-col>
      </v-row>
      <v-card class="rounded-xl pt-5">
        <ValidationObserver ref="observer">
          <form autocomplete="false" @submit.prevent="payOrder">
            <v-container>
                <v-row justify="space-between">
                  <v-col md="4" sm="4" xs="12">
                    <ValidationProvider
                      v-slot="{ errors }"
                      name="card"
                      rules="required"
                    >
                      <label for="card">Número de tarjeta</label>
                      <v-text-field
                        id="card"
                        v-model="pay.card"
                        placeholder="Todos los dígitos de su tarjeta"
                        outlined
                        dense
                        class="mx-auto rounded-lg"
                        :error-messages="errors"
                        :hide-details="errors.length > 0 ? false : true"
                      ></v-text-field>
                    </ValidationProvider>
                  </v-col>
                  <v-col md="4" sm="4" xs="12">
                    <ValidationProvider
                      v-slot="{ errors }"
                      name="digits"
                      rules="required"
                    >
                      <label for="digits">Dígitos de seguridad</label>
                      <v-text-field
                        id="digits"
                        v-model="pay.digits"
                        placeholder="CVV2/CVC2"
                        outlined
                        dense
                        class="mx-auto rounded-lg"
                        :error-messages="errors"
                        :hide-details="errors.length > 0 ? false : true"
                      ></v-text-field>
                    </ValidationProvider>
                  </v-col>
                  <v-col md="4" sm="4" xs="12">
                    <ValidationProvider
                      v-slot="{ errors }"
                      name="state"
                      rules="required"
                    >
                      <label for="state">Estado</label>
                      <v-select
                        id="state"
                        v-model="pay.state"
                        :items="states"
                        placeholder="Seleccionar estado"
                        outlined
                        dense
                        class="mx-auto rounded-lg"
                        no-data-text="No hay datos"
                        :error-messages="errors"
                        :hide-details="errors.length > 0 ? false : true"
                      ></v-select>
                    </ValidationProvider>
                  </v-col>
                </v-row>
                <v-row justify="space-between">
                  <v-col md="4" sm="4" xs="12">
                    <ValidationProvider
                      v-slot="{ errors }"
                      name="expiration_month"
                      rules="required"
                    >
                      <label for="expiration_month">Mes de vencimiento</label>
                      <v-text-field
                        id="expiration_month"
                        v-model="pay.expiration_month"
                        placeholder="Seleccionar mes"
                        outlined
                        dense
                        class="mx-auto rounded-lg"
                        :error-messages="errors"
                        :hide-details="errors.length > 0 ? false : true"
                      ></v-text-field>
                    </ValidationProvider>
                  </v-col>
                  <v-col md="4" sm="4" xs="12">
                    <ValidationProvider
                      v-slot="{ errors }"
                      name="expiration_year"
                      rules="required"
                    >
                    <label for="expiration_year">Año de vencimiento</label>
                      <v-text-field
                        id="expiration_year"
                        v-model="pay.expiration_year"
                        placeholder="Año de vencimiento"
                        outlined
                        dense
                        class="mx-auto rounded-lg"
                        :error-messages="errors"
                        :hide-details="errors.length > 0 ? false : true"
                      ></v-text-field>
                    </ValidationProvider>
                  </v-col>
                  <v-col md="4" sm="4" xs="12">
                    <ValidationProvider
                      v-slot="{ errors }"
                      name="city"
                      rules="required"
                    >
                      <label for="city">Ciudad</label>
                      <v-select
                        id="city"
                        v-model="pay.city"
                        :items="cities"
                        placeholder="Seleccione un estado"
                        outlined
                        dense
                        class="mx-auto rounded-lg"
                        no-data-text="No hay datos"
                        :error-messages="errors"
                        :hide-details="errors.length > 0 ? false : true"
                      ></v-select>
                    </ValidationProvider>
                  </v-col>
                </v-row>
                <v-row justify="space-between">
                  <v-col md="4" sm="4" xs="12">
                    <ValidationProvider
                      v-slot="{ errors }"
                      name="name"
                      rules="required"
                    >
                      <label for="name">Nombre</label>
                      <v-text-field
                        id="name"
                        v-model="pay.name"
                        placeholder="Escriva su nombre"
                        outlined
                        dense
                        class="mx-auto rounded-lg"
                        :error-messages="errors"
                        :hide-details="errors.length > 0 ? false : true"
                      ></v-text-field>
                    </ValidationProvider>
                  </v-col>
                  <v-col md="4" sm="4" xs="12">
                    <ValidationProvider
                      v-slot="{ errors }"
                      name="lastname"
                      rules="required"
                    >
                      <label for="lastname">Apellidos</label>
                      <v-text-field
                        id="lastname"
                        v-model="pay.lastname"
                        placeholder="Escriba sus apellidos"
                        outlined
                        dense
                        class="mx-auto rounded-lg"
                        :error-messages="errors"
                        :hide-details="errors.length > 0 ? false : true"
                      ></v-text-field>
                    </ValidationProvider>
                  </v-col>
                  <v-col md="4" sm="4" xs="12">
                    <ValidationProvider
                      v-slot="{ errors }"
                      name="postal_code"
                      rules="required"
                    >
                      <label for="lastname">Código postal</label>
                      <v-text-field
                        id="postal_code"
                        v-model="pay.postal_code"
                        placeholder="Código Zip"
                        outlined
                        dense
                        class="mx-auto rounded-lg"
                        :error-messages="errors"
                        :hide-details="errors.length > 0 ? false : true"
                      ></v-text-field>
                    </ValidationProvider>
                  </v-col>
                </v-row>
                <v-row justify="space-between">
                  <v-col md="4" sm="4" xs="12">
                    <ValidationProvider
                      v-slot="{ errors }"
                      name="address1"
                      rules="required"
                    >
                      <label for="address1">Dirección (Linea 1)</label>
                      <v-text-field
                        id="address1"
                        v-model="pay.address1"
                        placeholder="Dirección de registro"
                        outlined
                        dense
                        class="mx-auto rounded-lg"
                        :error-messages="errors"
                        :hide-details="errors.length > 0 ? false : true"
                      ></v-text-field>
                    </ValidationProvider>
                  </v-col>
                  <v-col md="4" sm="4" xs="12">
                    <ValidationProvider
                      v-slot="{ errors }"
                      name="address2"
                    >
                      <label for="address2">Dirección (Linea 2)</label>
                      <v-text-field
                        id="address2"
                        v-model="pay.address2"
                        label="Dirección (Linea 2)"
                        outlined
                        dense
                        class="mx-auto rounded-lg"
                        :error-messages="errors"
                        :hide-details="errors.length > 0 ? false : true"
                      ></v-text-field>
                    </ValidationProvider>
                  </v-col>
                  <v-col md="4" sm="4" xs="12" align="center" align-self="center" class="pt-3">
                    <h1 :class="this.$vuetify.breakpoint.name == 'sm' ? 'text-bolder text-end' : 'text-bolder text-end d-flex flex-row justify-end'"><p class="mr-5" style="color: black !important;">Total:</p><p class="text-info text-bold">$257.45</p></h1>
                  </v-col>
                </v-row>
                <v-row align="center" justify="space-between" class="py-5 px-3">
                  <v-btn type="button" class="mx-2 rounded-lg text-bold border-hidden" outlined rounded elevation="3" color="secondary" @click="cancel">Borrar todo</v-btn>
                  <p v-if="breakpoint" class="mb-0">Todos los capos son requeridos</p>
                  <v-btn type="submit" class="mx-2 rounded-lg text-bold" elevation="3" color="secondary">Pagar</v-btn>
                </v-row>
            </v-container>
          </form>
        </ValidationObserver>
      </v-card>
    </v-container>
  </v-main>
</template>

<script>
export default {
  name: 'PayOrder',
  data: function () {
    return {
      states: [
        'Florida',
        'Carolina',
        'Pensilbania'
      ],
      cities: [
        'Orlandos',
        'Jackson Ville',
        'Miami'
      ],
      pay: {
        card: null,
        digits: null,
        expiration_month: null,
        expiration_year: null,
        name: 'Alfredo Suarez',
        lastname: 'Garrido',
        address1: 'Cuba',
        address2: null,
        city: 'Miami',
        state: 'Florida',
        post_code: '58465468'
      }
    }
  },
  computed: {
    breakpoint () {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs': return false
        case 'sm': return false
        // case 'md': return false
        // case 'lg': return false
        // case 'xl': return false
      }
    }
  },
  methods: {
    payOrder () {
      this.$refs.observer.validate().then((result) => {
        if (result) {
          alert('Paid out!')
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.container{
  margin: 3rem auto;
}
.row{
  margin: 0 !important;
}
.col{
  padding-top: 0px;
  // padding-bottom: 0px;
}
h1 p{
  margin-bottom: 0 !important;
}
.flex-alignment{
  flex-direction: row !important;
  display: flex !important;
}
</style>
