<script>
import store from '../../../store'
// import MenuStore from '@/components/MenuStore'
import MenuProductsList from '@/components/MenuProductsList'
// import Navigation from '@/components/Navigation'

export default {
  name: 'Restaurant',
  // props: {
  //   product: {
  //     type: Object,
  //     required: true
  //   }
  // },
  data: function () {
    return {
      id: this.$route.params.id,
      product: this.$store.getters.getProduct(this.$route.params.id),
      imageSelected: null,
      sections: [
        { name: 'Ofertas' },
        { name: 'Entrantes' },
        { name: 'Bebidas' },
        { name: 'Platos principales' },
        { name: 'Pastas' },
        { name: 'Postres' },
        { name: 'Guarniciones' }
      ]
    }
  },
  components: {
    // MenuStore,
    MenuProductsList
    // Navigation
  },
  created: function () {
    this.imageSelected = this.product.images[0]
  },
  watch: {
    '$route' (to, from) {
      this.id = to.params.id
      this.product = this.$store.getters.getProduct(this.id)
      this.imageSelected = this.product.images[0]
    }
  },
  computed: {
    amoutn: {
      get () {
        return store.getters.checkProductOnCart(this.product.id)
      }
    },
    selectedImage: function () {
      console.log(this.product)
      return this.imageSelected
    },

    breakpoint () {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs': return true
        case 'sm': return true
        // case 'md': return false
        // case 'lg': return false
        // case 'xl': return false
      }
    }
  },
  methods: {
    addToCart (product) {
      this.$store.dispatch('addToCart', product)
    },
    checkOnFavorites (product) {
      if (product.isFavorite) {
        this.$store.dispatch('addToFavorites', product)
      } else {
        this.$store.dispatch('removeFromFavorites', product)
      }
      product.isFavorite = !product.isFavorite
    },
    getImgUrl (image) {
      var images = require.context('../../../assets/', false)
      return images('./' + image)
    },
    selectImage (imageIndex) {
      const temImg = this.product.images[0]
      this.product.images[imageIndex] = this.product.images[0]
      this.product.images[0] = temImg
      this.imageSelected = this.product.images[0]
    }
  }
}
</script>

