<script>
// import NewClient from './new-client'
import router from '../../../router'
// import { extend } from 'vee-validate'

// extend('termsAccepted', {
//   validate (value) {
//     return this.destinatary.alternative_destinatary.length == 0 && this.destinatary.deny_alternative_destinatary
//   },
//   message: 'Si no tiene destinatario alternativo es requerido este campo'
// })

export default {
  name: 'AcceptOrder',
  components: {
    // NewClient
  },
  data () {
    return {
      client: null,
      show: false,
      date: null,
      date_menu: null,
      provinces: [
        'La Habana'
      ],
      municipalities: [
        'Playa',
        'Plaza',
        'Marianao'
      ],
      destinatary: {
        name: 'Alfredo Suarez',
        lastname: 'Garrido',
        phone: '+1 568 5546 568',
        id: '89565123165',
        address1: 'Cuba',
        address2: null,
        province: 'La habana',
        state: 'Playa',
        alternative_destinatary: 'Yazmin Reyes',
        delivery_date: null,
        deny_alternative_destinatary: false
      },
      clients: [
        'Ramon Hernandez   (Destinatario alternativo: Gabril Cruz)',
        'Juan Cabrales',
        'Maria La Pinta',
        'Pedro Pablo   (Destinatario alternativo: Juana Lopez)'
      ],
      headers: [
        { text: 'Producto', align: 'center', value: 'title' },
        { text: '', align: 'center', value: 'divider' },
        { text: 'Cantidad', align: 'center', value: 'amount' },
        { text: '', align: 'center', value: 'divider' },
        { text: 'Precio x producto', align: 'center', value: 'price' },
        { text: '', align: 'center', value: 'divider' },
        { text: 'Subtotal', align: 'center', value: 'subtotal' }
      ],
      products: []
    }
  },
  created () {
    var cart = this.$store.getters.cart()
    cart.map((p) => {
      p.count = this.$store.getters.checkProductOnCart(p.id) || 0
      return p
    })
    cart = [...new Set(cart)]
    this.products = cart
  },
  watch: {
    date (val) {
      this.destinatary.delivery_date = this.formatDate(this.date)
    }
  },
  methods: {
    makeRequest () {
      // const valid = true

      this.$refs.observer.validate().then((result) => {
        if (result) {
          if (this.destinatary.alternative_destinatary.length === 0 && !this.destinatary.deny_alternative_destinatary) {
            this.$refs.no_alternative.applyResult({
              errors: ['Si no tiene destinatario alternativo es requerido este campo'],
              valid: false, // boolean state
              failedRules: {}
            })
          } else {
            router.push({ name: 'order-pay' })
          }
        }
      })
    },
    addNewDestinatary (event) {
      console.log(event)
      this.show = false
      this.clients.push(event)
      this.client.push(event)
    },
    removeFromCart (product) {
      this.$store.dispatch('removeFromCart', product)
      this.products = this.$store.getters.cart()
    },
    formatDate (date) {
      if (!date) return null

      const [year, month, day] = date.split('-')
      return `${day}/${month}/${year}`
    },
    parseDate (date) {
      if (!date) return null

      const [day, month, year] = date.split('/')
      return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
    }
  }
}
</script>

