<template>
  <div>
    <div v-if="!createNewRestaurant">
      <v-data-table
        v-model="selected"
        :headers="headers"
        :items="restaurants"
        item-key="name"
        show-select
        class="elevation-0"
        :search="search"
        no-data-text="No hay restaurantes"
        no-results-text="Sin resultados"
      >
        <!-- Top row -->
        <template v-slot:top>
          <v-row align="center">
            <h2 class="text-bolder">LISTA DE RESTAURANTES</h2>
            <v-spacer></v-spacer>
            <v-text-field
              v-model="search"
              append-icon="mdi-magnify"
              label="Buscar restaurante..."
              class="mx-4"
              single-line
              hide-details
            ></v-text-field>
          </v-row>

          <!-- Modal for edit action -->
          <v-row align="center" justify="end">
            <v-btn
              v-if="selected.length == 0"
              color="primary"
              class="mb-9 rounded-lg text-bold"
              @click="createNewRestaurant = true"
            >
              Agregar restaurante
            </v-btn>

            <v-dialog
              v-model="dialog"
              max-width="500px"
              v-if="selected.length > 0"
            >
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  color="error"
                  class="mb-9 rounded-lg text-bold"
                  v-bind="attrs"
                  v-on="on"
                >
                  Eliminar seleccionados
                </v-btn>
              </template>
              <v-card>
                <v-card-title>
                  <span class="headline">Seguro que desea eliminar los contactos seleccionados?</span>
                </v-card-title>

                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn
                    class="text-bolder"
                    color="blue darken-1"
                    text
                    @click="close"
                  >
                    Cancelar
                  </v-btn>
                  <v-btn
                    class="text-bolder"
                    color="blue darken-1"
                    text
                    @click="deleteSelectedContactos"
                  >
                    Eliminar
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
          </v-row>
        </template>

        <template v-slot:item.image="{ item }">
          <v-img width="150" max-width="150" height="150" max-height="150" :src="item.images.length > 0 ? getImgUrl(item.images[0]) : getImgUrl('no_restaurant_image.jpg')" :lazy-src="item.images.length > 0 ? getImgUrl(item.images[0]) : getImgUrl('no_restaurant_image.jpg')"></v-img>
        </template>

        <!-- Actions column -->
        <template v-slot:item.actions="{ item }">
          <v-icon
            small
            class="mr-2"
            @click="editItem(item)"
          >
            mdi-pencil
          </v-icon>
          <v-icon
            small
            @click="deleteItem(item)"
          >
            mdi-delete
          </v-icon>
        </template>

      </v-data-table>

      <!-- Modal for delete action -->
      <v-dialog v-model="dialogDelete" max-width="500px">
        <v-card>
          <v-card-title class="headline">Seguro que desea elimnar "{{ editedItem.name }}"</v-card-title>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn class="text-bolder" color="blue darken-1" text @click="closeDelete">Cancelar</v-btn>
            <v-btn class="text-bolder" color="blue darken-1" text @click="deleteItemConfirm">Eliminar</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </div>

    <CreateRestaurant v-if="createNewRestaurant" :editedItem="editedItem" @save="save" @close="close" />
  </div>
</template>

<script>
import { dateRules } from '@/mixins/date_rules'
import { numberRules } from '@/mixins/number_rules'
import { generalRules } from '@/mixins/general_rules'
import CreateRestaurant from './CreateRestaurant'

export default {
  name: 'RestaurantAdmin',
  mixins: [numberRules, dateRules, generalRules],
  components: {
    CreateRestaurant
  },
  data () {
    return {
      search: '',
      selected: [],
      address: '',
      createNewRestaurant: false,
      dialog: false,
      dialogDelete: false,
      editedIndex: -1,
      editedItem: {
        id: null,
        name: null,
        description: null,
        address1: null,
        address2: null,
        province: null,
        municipality: null,
        phone: null,
        email: null,
        web: null,
        food_type: null,
        price_range: null,
        schedule: null,
        open_hour: null,
        closed_hour: null,
        home_delivery: null,
        delivery_time: null,
        delivery_time_unit: null,
        delivery_max_time: null,
        delivery_max_time_unit: null,
        delivery_distance_limit: null,
        images: ['no_restaurant_image.jpg']
      },
      defaultItem: {
        id: null,
        name: null,
        description: null,
        address1: null,
        address2: null,
        province: null,
        municipality: null,
        phone: null,
        email: null,
        web: null,
        food_type: null,
        price_range: null,
        schedule: null,
        open_hour: null,
        closed_hour: null,
        home_delivery: null,
        delivery_time: null,
        delivery_time_unit: null,
        delivery_max_time: null,
        delivery_max_time_unit: null,
        delivery_distance_limit: null,
        images: ['no_restaurant_image.jpg']
      },
      restaurants: [
        {
          id: 123355678,
          name: 'Frozen Yogurt1',
          schedule: '8:00 am - 5:30 pm',
          open_hour: null,
          closed_hour: null,
          price_range: '$20 - $250',
          food_type: 'China',
          description: null,
          address1: null,
          address2: null,
          province: null,
          municipality: null,
          phone: null,
          email: null,
          web: null,
          home_delivery: null,
          delivery_time: null,
          delivery_time_unit: null,
          delivery_max_time: null,
          delivery_max_time_unit: null,
          delivery_distance_limit: null,
          images: [
            'combo_mixto.png',
            'cubamax.jpeg',
            'combo_mixto.png'
          ]
        },
        {
          id: 123355678,
          name: 'Frozen Yogurt2',
          schedule: '8:00 am - 5:30 pm',
          open_hour: null,
          closed_hour: null,
          price_range: '$20 - $250',
          food_type: 'China',
          images: [
            'cubamax.jpeg',
            'combo_mixto.png'
          ],
          description: null,
          address1: null,
          address2: null,
          province: null,
          municipality: null,
          phone: null,
          email: null,
          web: null,
          home_delivery: null,
          delivery_time: null,
          delivery_time_unit: null,
          delivery_max_time: null,
          delivery_max_time_unit: null,
          delivery_distance_limit: null
        },
        {
          id: 123355678,
          name: 'Frozen Yogurt3',
          schedule: '8:00 am - 5:30 pm',
          open_hour: null,
          closed_hour: null,
          price_range: '$20 - $250',
          food_type: 'China',
          images: [
            'combo_mixto.png',
            'cubamax.jpeg',
            'combo_mixto.png'
          ],
          description: null,
          address1: null,
          address2: null,
          province: null,
          municipality: null,
          phone: null,
          email: null,
          web: null,
          home_delivery: null,
          delivery_time: null,
          delivery_time_unit: null,
          delivery_max_time: null,
          delivery_max_time_unit: null,
          delivery_distance_limit: null
        },
        {
          id: 123355678,
          name: 'Frozen Yogurt4',
          schedule: '8:00 am - 5:30 pm',
          open_hour: null,
          closed_hour: null,
          price_range: '$20 - $250',
          food_type: 'China',
          images: [
            'combo_mixto.png',
            'cubamax.jpeg',
            'combo_mixto.png'
          ],
          description: null,
          address1: null,
          address2: null,
          province: null,
          municipality: null,
          phone: null,
          email: null,
          web: null,
          home_delivery: null,
          delivery_time: null,
          delivery_time_unit: null,
          delivery_max_time: null,
          delivery_max_time_unit: null,
          delivery_distance_limit: null
        }
      ],
      options: {
        duration: 300,
        offset: 0,
        easing: 'easeInOutCubic'
      }
    }
  },
  watch: {
    dialog (val) {
      val || this.close()
    },

    dialogDelete (val) {
      val || this.closeDelete()
    }
  },
  created () {
  },
  computed: {
    formTitle () {
      return this.editedIndex === -1 ? 'Nuevo contacto' : 'Editar contacto'
    },

    headers () {
      return [
        {
          text: '',
          align: 'center',
          sortable: false,
          value: 'image'
        },
        {
          text: 'Nombre',
          align: 'start',
          sortable: true,
          value: 'name'
        },
        {
          text: 'Horario',
          align: 'center',
          sortable: true,
          value: 'schedule'
        },
        {
          text: 'Rango de precios',
          align: 'center',
          value: 'price_range',
          sortable: true
        },
        { text: 'Tipo de cocina', align: 'center', value: 'food_type', sortable: true },
        { text: '', align: 'center', value: 'actions', sortable: false }
      ]
    }
  },
  methods: {
    filterOnlyCapsText (value, search, item) {
      return value != null &&
          search != null &&
          typeof value === 'string' &&
          value.toString().toLocaleUpperCase().indexOf(search) !== -1
    },

    createItem () {
      this.$emit('create')
    },

    editItem (item) {
      this.editedIndex = this.restaurants.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.createNewRestaurant = true
    },

    deleteItem (item) {
      this.editedIndex = this.restaurants.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialogDelete = true
    },

    deleteItemConfirm () {
      this.restaurants.splice(this.editedIndex, 1)
      this.closeDelete()
    },

    close () {
      this.dialog = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
      this.createNewRestaurant = false
      this.$vuetify.goTo('#general', this.options)
    },

    closeDelete () {
      this.dialogDelete = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },

    save () {
      if (this.editedIndex > -1) {
        Object.assign(this.restaurants[this.editedIndex], this.editedItem)
      } else {
        this.restaurants.push(this.editedItem)
      }
      this.createNewRestaurant = false
    },

    getImgUrl (image) {
      var images = require.context('../../../../assets/', false)
      return images('./' + image)
    }
  }
}
</script>

<style lang="scss" scoped>
.v-btn{
  font-size: 11px;
}
.row{
  margin: 18px 12px;
}
.theme--light.v-data-table > .v-data-table__wrapper > table > thead > tr:last-child > th:first-child {
  width: 50px !important;
  text-align: center;
}
</style>
