<template>
  <v-container class="mt-10">
    <ValidationObserver ref="observer">
      <form autocomplete="off">
        <FileUpload />

        <!-- Nombre y telefono -->
        <v-row justify="space-between">
          <v-col md="7" sm="12" xs="12">
            <ValidationProvider
              v-slot="{ errors }"
              name="name"
              rules="required"
            >
              <label for="name">Nombre</label>
              <v-text-field
                ref="name"
                id="name"
                v-model="editedItem.name"
                placeholder="Nombre del restaurante"
                outlined
                dense
                class="mx-auto rounded-lg"
                :error-messages="errors"
                :hide-details="errors.length > 0 ? false : true"
              >
              </v-text-field>
            </ValidationProvider>
          </v-col>
        </v-row>

       <!-- Nombre y telefono -->
        <v-row>
          <v-col md="2" sm="4" xs="12">
            <ValidationProvider
              v-slot="{ errors }"
              name="price"
              rules="required"
            >
              <label for="price">Precio</label>
              <v-text-field
                ref="price"
                id="price"
                v-model="editedItem.price"
                placeholder=""
                type="number"
                outlined
                dense
                prefix="$"
                class="mx-auto rounded-lg"
                :error-messages="errors"
                :hide-details="errors.length > 0 ? false : true"
              >
              </v-text-field>
            </ValidationProvider>
          </v-col>
          <v-col md="5" sm="6" xs="12">
            <ValidationProvider
              v-slot="{ errors }"
              name="category"
              rules="required"
            >
              <label for="category">Categoría</label>
              <v-select
                ref="category"
                id="category"
                v-model="editedItem.category"
                :items="categories"
                placeholder="Seleccionar categoría"
                outlined
                dense
                class="mx-auto rounded-lg"
                no-data-text="No hay datos"
                :error-messages="errors"
                :hide-details="errors.length > 0 ? false : true"
              ></v-select>
            </ValidationProvider>
          </v-col>
        </v-row>

        <!-- Descripcion -->
        <v-row>
          <v-col cols="12">
            <label for="description">Descripción</label>
            <v-textarea
              ref="description"
              id="description"
              name="description"
              v-model="editedItem.description"
              placeholder="Breve descripcion del producto"
              outlined
              hide-details
              class="rounded-lg"
            ></v-textarea>
          </v-col>
        </v-row>

        <v-row class="my-15" justify="center">
          <v-btn type="button" class="mx-2" rounded elevation="0" color="error" @click="close">Cancelar</v-btn>
          <v-btn type="button" class="mx-2" rounded elevation="0" color="primary" @click="save">Guardar</v-btn>
        </v-row>
      </form>
    </ValidationObserver>
  </v-container>
</template>

<script>
import { dateRules } from '@/mixins/date_rules'
import { numberRules } from '@/mixins/number_rules'
import { generalRules } from '@/mixins/general_rules'
import FileUpload from '@/components/image-upload/FileUpload'

export default {
  name: 'CreateMenu',
  components: {
    FileUpload
  },
  props: {
    editedItem: {
      type: Object,
      required: true
    }
  },
  data () {
    return {
      categories: [
        'Entrantes',
        'Plato fuerte',
        'Postres'
      ]
    }
  },
  mixins: [numberRules, dateRules, generalRules],
  methods: {
    close () {
      for (const key in this.$refs) {
        if (key !== 'observer' && key !== 'open_date_menu' && key !== 'closed_date_menu') {
          this.$refs[key].reset()
        }
      }
      this.$emit('close')
    },

    save () {
      this.$refs.observer.validate().then((result) => {
        if (result) {
          this.$emit('save')
        }
      })
    },

    formatDate (date) {
      if (!date) return null

      const [year, month, day] = date.split('-')
      return `${day}/${month}/${year}`
    },

    parseDate (date) {
      if (!date) return null

      const [day, month, year] = date.split('/')
      return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
    }
  }
}
</script>

