<template>
  <div>
    <v-data-table
      v-model="selected"
      :headers="headers"
      :items="contacts"
      item-key="name"
      show-select
      class="elevation-0"
      :search="search"
      no-data-text="No hay contactos"
      no-results-text="Sin resultados"
    >
      <!-- Top row -->
      <template v-slot:top>
        <v-row align="center">
          <h2 class="text-bolder">LISTA DE CONTACTOS</h2>
          <v-spacer></v-spacer>
          <v-text-field
            v-model="search"
            append-icon="mdi-magnify"
            label="Buscar contacto"
            class="mx-4"
            single-line
            hide-details
          ></v-text-field>
        </v-row>

        <!-- Modal for edit action -->
        <v-row align="center" justify="end">
          <v-dialog
            v-model="dialog"
            max-width="500px"
            scrollable
            v-if="selected.length == 0"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                color="primary"
                class="mb-9 rounded-lg text-bold"
                v-bind="attrs"
                v-on="on"
              >
                Agregar contacto
              </v-btn>
            </template>
            <v-card>
              <v-card-title>
                <span class="headline">{{ formTitle }}</span>
              </v-card-title>

              <v-card-text>
                <v-container>
                  <v-row>
                    <v-col
                      cols="12"
                    >
                      <v-text-field
                        ref="name"
                        v-model="editedItem.name"
                        label="Nombre"
                        dense
                        rounded
                        outlined
                        hide-details
                      ></v-text-field>
                    </v-col>
                    <v-col
                      cols="12"
                    >
                      <v-text-field
                        v-model="editedItem.phone"
                        label="Teléfono"
                        dense
                        rounded
                        outlined
                        hide-details
                      ></v-text-field>
                    </v-col>
                    <v-col
                      cols="12"
                    >
                      <v-textarea
                        v-model="editedItem.address"
                        label="Dirección"
                        rows="1"
                        auto-grow
                        dense
                        rounded
                        outlined
                        hide-details
                      ></v-textarea>
                    </v-col>
                    <v-col
                      cols="12"
                    >
                      <v-text-field
                        v-model="editedItem.id"
                        label="ID"
                        dense
                        rounded
                        outlined
                        hide-details
                        :rules="[id_number]"
                      ></v-text-field>
                    </v-col>
                    <v-col
                      cols="12"
                    >
                      <v-text-field
                        v-model="editedItem.card"
                        label="Tarjeta AIS"
                        dense
                        rounded
                        outlined
                        hide-details
                        :rules="[card]"
                      ></v-text-field>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>

              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn
                  color="blue darken-1"
                  text
                  @click="close"
                >
                  Cancelar
                </v-btn>
                <v-btn
                  color="blue darken-1"
                  text
                  @click="save"
                >
                  Guardar
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>

          <v-dialog
            v-model="dialog"
            max-width="500px"
            v-if="selected.length > 0"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                color="error"
                class="mb-9 rounded-lg text-bold"
                v-bind="attrs"
                v-on="on"
              >
                Eliminar seleccionados
              </v-btn>
            </template>
            <v-card>
              <v-card-title>
                <span class="headline">Seguro que desea eliminar los contactos seleccionados?</span>
              </v-card-title>

              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn
                  color="blue darken-1"
                  text
                  @click="close"
                >
                  Cancelar
                </v-btn>
                <v-btn
                  color="blue darken-1"
                  text
                  @click="deleteSelectedContactos"
                >
                  Eliminar
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-row>
      </template>

      <template v-slot:item.address="{ item }">
        <v-col>
          <p class="mb-0 text-start">{{ item.address1 }}</p>
          <p class="mb-0 text-start">{{ item.address2 }}</p>
        </v-col>
      </template>

      <!-- Actions column -->
      <template v-slot:item.actions="{ item }">
        <v-icon
          small
          class="mr-2"
          @click="editItem(item)"
        >
          mdi-pencil
        </v-icon>
        <v-icon
          small
          @click="deleteItem(item)"
        >
          mdi-delete
        </v-icon>
      </template>

    </v-data-table>

    <!-- Modal for delete action -->
    <v-dialog v-model="dialogDelete" max-width="500px">
      <v-card>
        <v-card-title class="headline">Are you sure you want to delete this item?</v-card-title>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="closeDelete">Cancelar</v-btn>
          <v-btn color="blue darken-1" text @click="deleteItemConfirm">Eliminar</v-btn>
          <v-spacer></v-spacer>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { dateRules } from '@/mixins/date_rules'
import { numberRules } from '@/mixins/number_rules'
import { generalRules } from '@/mixins/general_rules'

export default {
  name: 'Destinataries',
  mixins: [numberRules, dateRules, generalRules],
  components: {
  },
  data () {
    return {
      search: '',
      selected: [],
      address: '',
      dialog: false,
      dialogDelete: false,
      editedIndex: -1,
      editedItem: {
        name: '',
        address: '',
        id: '',
        phone: '',
        card: ''
      },
      defaultItem: {
        name: '',
        address: '',
        id: '',
        phone: '',
        card: ''
      },
      contacts: [
        {
          name: 'Frozen Yogurt1',
          address1: 'Calle 1 entre Calle 2 y Calle 3 #1234.',
          address2: 'Reparto, Municipio. Provincia.',
          id: 123355678,
          phone: 1234567,
          card: 123456789
        },
        {
          name: 'Frozen Yogurt2',
          address1: 'Calle 1 entre Calle 2 y Calle 3 #1234.',
          address2: 'Reparto, Municipio. Provincia.',
          id: 123355678,
          phone: 1234567,
          card: 123456789
        },
        {
          name: 'Frozen Yogurt3',
          address1: 'Calle 1 entre Calle 2 y Calle 3 #1234.',
          address2: 'Reparto, Municipio. Provincia.',
          id: 123355678,
          phone: 1234567,
          card: 123456789
        },
        {
          name: 'Frozen Yogurt4',
          address1: 'Calle 1 entre Calle 2 y Calle 3 #1234.',
          address2: 'Reparto, Municipio. Provincia.',
          id: 123355678,
          phone: 1234567,
          card: 123456789
        }
      ]
    }
  },
  watch: {
    dialog (val) {
      val || this.close()
    },
    dialogDelete (val) {
      val || this.closeDelete()
    }
  },
  created () {
    this.initialize()
  },
  computed: {
    formTitle () {
      return this.editedIndex === -1 ? 'Nuevo contacto' : 'Editar contacto'
    },
    headers () {
      return [
        {
          text: 'Nombre',
          align: 'start',
          sortable: true,
          value: 'name'
        },
        {
          text: 'Teléfono',
          align: 'center',
          sortable: true,
          value: 'phone'
        },
        {
          text: 'Dirección',
          align: 'center',
          value: 'address',
          sortable: true
        },
        { text: 'No. de CI', align: 'center', value: 'id', sortable: true },
        { text: 'Tarjeta AIS', align: 'center', value: 'card', sortable: true },
        { text: '', align: 'center', value: 'actions', sortable: false }
      ]
    }
  },
  methods: {
    filterOnlyCapsText (value, search, item) {
      return value != null &&
          search != null &&
          typeof value === 'string' &&
          value.toString().toLocaleUpperCase().indexOf(search) !== -1
    },
    editItem (item) {
      this.editedIndex = this.contacts.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialog = true
    },

    deleteItem (item) {
      this.editedIndex = this.contacts.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialogDelete = true
    },

    deleteItemConfirm () {
      this.contacts.splice(this.editedIndex, 1)
      this.closeDelete()
    },

    close () {
      this.dialog = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },

    closeDelete () {
      this.dialogDelete = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },

    save () {
      if (this.editedIndex > -1) {
        Object.assign(this.contacts[this.editedIndex], this.editedItem)
      } else {
        this.contacts.push(this.editedItem)
      }
    },

    deleteSelectedContactos () {
      // if (this.editedIndex > -1) {
      //   Object.assign(this.contacts[this.editedIndex], this.editedItem)
      // } else {
      //   this.contacts.push(this.editedItem)
      // }
      this.close()
    }
  }
}
</script>

<style lang="scss" scoped>
.v-btn{
  font-size: 11px;
}
.row{
  margin: 18px 12px;
}
.theme--light.v-data-table > .v-data-table__wrapper > table > thead > tr:last-child > th:first-child {
  width: 50px !important;
  text-align: center;
}
</style>
