<template>

    <!-- Sizes your content based upon application components -->
    <v-main id="home">

        <v-dialog
          v-model="dialog"
          persistent
          max-width="700"
        >
          <v-card>
            <v-card-title class="headline">
              Seleccione el destino
            </v-card-title>

            <v-card-text>
              <v-row align="center" justify="center">
