                    dense
                    outlined
                    rounded
                    label="Seleccionar provincia"
                    class="mx-2"
                    hide-details
                    :menu-props="{ 'offset-y': true }"
                  >
                  </v-select>
                </v-col>
                <v-col align="center" align-self="center">
                  <v-select
                    id="municipalities"
                    v-model="municipality"
                    :items="municipalities"
                    color="primary"
                    dense
                    outlined
                    rounded
                    label="Seleccionar municipio"
                    class="mx-2"
                    hide-details
                    :disabled="province != null ? false : true"
                    :menu-props="{ 'offset-y': true }"
                  >
                  </v-select>
                </v-col>
              </v-row>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>

              <v-btn
                color="primary"
                text
                @click="dialog = false"
              >
                Buscar
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>

        <v-container>

