            <Carrousel/>

            <v-row class="pt-4">
              <h2 id="title">Nuestros restaurantes</h2>
            </v-row>

            <v-row align="center" justify="space-around">
              <v-col cols="7">
                  FILTRAR POR:
              </v-col>
              <v-col align="center" align-self="center" cols="3">
                <v-text-field
                  v-model="search"
                  append-icon="mdi-magnify"
                  label="Buscar..."
                  hide-details
