                  class="mt-0 pt-0"
                ></v-text-field>
              </v-col>
            </v-row>

            <v-row class="filters" align="center">
              <v-col>
                  <v-select
                      id="provinces"
                      v-model="province"
                      label="Provincia"
                      :items="provinces"
                      color="primary"
                      dense
                      outlined
                      hide-details
                      :menu-props="{ 'offset-y': true }"
                    >
                  </v-select>
