                  </v-select>
              </v-col>
            </v-row>

            <v-row class="filters">
              <v-col cols="auto">
                <v-select
                  :items="food_types"
                  label="Tipo de comida"
                  dense
                  outlined
                  hide-details
                  :menu-props="{ 'offset-y': true }"
                ></v-select>
              </v-col>
              <v-divider vertical inset></v-divider>
              <v-col cols="auto">
                <v-select
                  :items="food_types"
                  label="Rango de precio"
                  dense
                  outlined
                  hide-details
                  :menu-props="{ 'offset-y': true }"
                ></v-select>
              </v-col>
              <v-divider vertical inset></v-divider>
              <v-col cols="auto">
                <v-select
                    :items="food_types"
                    label="Tipo de entrega"
                    dense
                    outlined
                    hide-details
                    :menu-props="{ 'offset-y': true }"
                  ></v-select>
              </v-col>
            </v-row>
        </v-container>
            <br>

          <v-container id="restaurants-container" fluid>
            <MenuProductsList :slide-mode="true" :list-size="6"/>
            <MenuProductsList :slide-mode="true" :list-size="6"/>
            <MenuProductsList :slide-mode="true" :list-size="6"/>
            <MenuProductsList :slide-mode="true" :list-size="6"/>

        </v-container>
    </v-main>
</template>

<script>
import Carrousel from './Carrousel'
import MenuProductsList from '@/components/MenuProductsList'
// import DownloadApp from './DownloadApp'
// import Trace from './Trace'
// import Raffle from './Raffle'
// import NewsSection from './NewsSection'
// import AboutCompany from './AboutCompany'
// import OfficesList from '@/components/OfficesList'
// import MostUsedServices from '@/components/MostUsedServices'

export default {
  name: 'Home',
  components: {
    Carrousel,
    MenuProductsList
    // Trace,
    // DownloadApp,
    // Raffle,
    // NewsSection,
    // AboutCompany,
    // OfficesList,
    // MostUsedServices
  },
  data () {
    return {
      dialog: true,
      search: null,
      province: null,
      municipality: null,
      provinces: [
        'La Habana'
      ],
      municipalities: [
        'Playa',
        'Plaza',
        'Marianao'
      ],
      food_types: [
        'Comida criolla',
        'italiana',
        'Cubana',
        'China'
      ],
      restaurants: []
    }
  }
}
</script>

<style scoped>
#restaurants-container{
  /* background-image: url('../../../assets/store/background-tienda-detail.png') !important; */
  background-image: url('../../../assets/restaurant/home-background.png') !important;
  background-size: cover;
  height: 85%;
}
.row{
  margin: 0;
}
#title{
    white-space: nowrap;
    text-align: left;
    font-family: Raleway-bold;
    font-style: normal;
    font-weight: bold;
    font-size: 32px;
    color: rgba(0,100,100,1);
    text-transform: uppercase;
}
.filters .col{
  max-width: 252px;
}
</style>
