import axiosCustom from '../axios'

export default class RestaurantServices {
  static getRestaurants (data) {
    return axiosCustom.get('/api/service/restaurant', { params: data })
  }

  static getRestaurantById (data) {
    return axiosCustom.get(`/api/service/restaurant/${data.id}`)
  }

  static addRestaurant (data) {
    return axiosCustom.post('/api/service/restaurant', data)
  }

  static updateRestaurant (data) {
    return axiosCustom.patch(`/api/service/restaurant/${data.id}`, data.object)
  }

  static deleteRestaurant (data) {
    return axiosCustom.delete(`/api/service/restaurant/${data.id}`)
  }
}
