                        Mover a la lista de deseos
                      </v-btn>
                    </v-col>
                  </v-row>

                </v-col>
              </v-row>
            </v-col>
          </v-row>
        </v-col>

        <v-col cols="2">
          <v-row justify="center"><p>Precio x unidad</p></v-row>
          <v-row justify="center"><p>${{ product.price }}</p></v-row>
        </v-col>

        <v-col cols="2">
          <v-row justify="center">Cantidad</v-row>
          <v-row justify="center">
            <v-text-field
              id="adults"
              v-model="product.amount"
              type="number"
              color="primary"
              outlined
              dense
              min="1"
              style="max-width: 50% !important;"
