          <v-row justify="center">${{ product.price + 1.60 }}</v-row>
        </v-col>

      </v-row> -->

      <v-data-table
        :headers="headers"
        :items="products"
        hide-default-header
        hide-default-footer
        :search="search"
        class="elevation-0"
      >

        <!-- Top row -->
        <template v-slot:top>
          <v-row class="mb-15" align="center" justify="end">
            <v-col md="6" sm="8" xs="12">
              <v-text-field
                v-model="search"
                append-icon="mdi-magnify"
                label="Buscar producto"
                class="mx-4"
                single-line
                clearable
                hide-details
              ></v-text-field>
            </v-col>
          </v-row>
        </template>

        <template v-slot:item.actions="{ item }">
          <v-col align="center">
            <v-btn class="text-bold close-btn" outlined color="secondary">
              <v-icon>mdi-close</v-icon> Eliminar
            </v-btn>
          </v-col>
        </template>

        <template v-slot:item.product="{ item }">
          <v-col id="horizontal-card" align="center" class="px-0">
            <v-row class="ma-0" align="center">
              <v-col cols="5" align="center" class="pa-0" v-if="!breakpoint">
                <v-img class="rounded-xl" height="154" max-height="154" width="200px" max-width="200px" :src="getImgUrl(item.images[0])" :lazy-src="getImgUrl(item.images[0])"></v-img>
              </v-col>
              <v-col id="info" align="center">
                  <p id="title" @click="goToDetails(item)">{{ item.title }}</p>
                  <p id="product-info">{{ item.description }}</p>
              </v-col>
            </v-row>
          </v-col>
        </template>

