            <span class="mx-3">$250.00</span>
          </v-col>
        </v-row>
        <v-row align="center" justify="end">
          <v-col cols="5" class="text-end">
            <span>Precio de Envío:</span>
          </v-col>
          <v-col cols="2" class="text-end text-info text-bold">
            <span class="mx-3">$5.00</span>
          </v-col>
        </v-row>
        <v-row align="center" justify="end">
          <v-col cols="5" class="text-end">
            <span>Impuesto por la compra:</span>
          </v-col>
          <v-col cols="2" class="text-end text-info text-bold">
            <span class="mx-3">$2.45</span>
          </v-col>
        </v-row>
        <v-row align="center" justify="end">
          <v-col cols="6">
            <v-divider class="mt-3" inset></v-divider>
          </v-col>
        </v-row>
        <v-row align="center" justify="end">
          <v-col cols="5" class="text-end text-bold" style="font-family: 'Raleway-bold">
            <span>Total</span>
          </v-col>
          <v-col cols="2" class="text-end text-info text-bold">
            <span class="mx-3">$257.45</span>
          </v-col>
        </v-row>
      </div>

      <v-row class="my-16" justify="space-between" align="center">
        <span id="comment">*El precio final dependerá del costo del envío</span
        >
        <v-spacer></v-spacer>
        <v-btn class="text-bold close-btn mx-2" outlined color="secondary" to="/restaurant">
          <v-icon class="mr-2">mdi-arrow-left</v-icon> Continuar comprando
        </v-btn>
        <v-btn :class="products.length > 0 ? 'text-bold mx-2 pr-3 close-btn' : 'text-bold mx-2 pr-3'"
                color="primary"
                :disabled="products.length === 0"
                to="/order/confirm">
          Pagar <v-icon class="ml-2">mdi-arrow-right</v-icon>
        </v-btn>
      </v-row>

    </v-container>
  </v-main>
</template>

<script>
// import { mapState } from 'vuex'

export default {
  name: 'Cart',
  data () {
    return {
      pagination: {
        limit: 12,
        offset: 0,
        page: 1,
        filter: null,
        order: '-name',
        total: 0
      },
      search: null,
      headers: [
        { text: '', align: 'center', value: 'actions' },
        { text: '', align: 'center', value: 'product' },
        { text: '', align: 'center', value: 'divider' },
        { text: '', align: 'center', value: 'amount' },
        { text: '', align: 'center', value: 'divider' },
        { text: '', align: 'center', value: 'price' },
        { text: '', align: 'center', value: 'divider' },
        { text: '', align: 'center', value: 'subtotal' }
      ],
      products: this.$store.getters.cart(),
      pageProducts: []
    }
  },
  computed: {
    breakpoint () {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs': return true
        case 'sm': return true
        case 'md': return true
        // case 'lg': return false
        // case 'xl': return false
      }
      return false
    }
  },
  // computed: mapState(['cart']),
  // watch: {
  //   cart (newValue, oldValue) {
  //     console.log('old', oldValue)
  //     console.log('new', newValue)
  //   }
  // },
  // created () {
  //   var cart = this.$store.getters.cart()
  //   cart.map((p) => {
  //     p.count = this.$store.getters.checkProductOnCart(p.id) || 0
  //     return p
  //   })
  //   cart = [...new Set(cart)]
  //   this.products = cart
  // },
  methods: {
    getImgUrl (image) {
      var images = require.context('../../../assets', false)
      return images('./' + image)
    },
    clearCart () {
      this.$store.dispatch('clearCart')
      this.products = this.$store.getters.cart()
    },
    checkOnFavorites (product) {
      if (this.$store.getters.checkProductOnFavorites(product.id) === 0) {
        this.$store.dispatch('addToFavoriteProducts', product)
        product.isFavorite = true
      } else {
        this.$store.dispatch('removeFromFavoriteProducts', product)
        product.isFavorite = false
      }
      this.removeFromCart(product)
    },
    removeFromCart (product) {
      this.$store.dispatch('removeFromCart', product)
      this.products = this.$store.getters.cart()
    }
  }
}
</script>

