<template>
  <v-main>
    <v-container id="wish-list-container" class="py-14">
      <h1 class="text-bolder">Lista de deseos</h1>
      <v-toolbar elevation="0" class="mt-16 px-10">
        <v-slide-group
          class="pa-2 d-flex flex-row align-center"
          center-active
          active-class="success"
          style="width: 100%"
          mandatory
          show-arrows
        >
          <v-slide-item v-slot="{ toggle }"><v-btn text color="primary" class="text-bold" @click="toggle">Todos</v-btn></v-slide-item>
          <v-slide-item v-slot="{ toggle }"><v-btn text color="primary" class="text-bold" @click="toggle">Restaurante</v-btn></v-slide-item>
        </v-slide-group>
      </v-toolbar>

        <v-row v-if="!slideMode && !listSize" class="my-7 justify-center" dense>
          <v-col cols="10">
            <v-row v-for="(product, i) in products" :key="i" align="center" justify="center">
              <v-col cols="auto">
                <v-btn max-width="55" min-width="55" class="border-hidden rounded-lg" outlined elevation="3"
                       color="secondary"
                       style="background: white !important"
                       @click="removeFromFavoriteProducts(product)">
                  <v-icon>mdi-close</v-icon>
                </v-btn>
              </v-col>
              <v-col>
                <MenuProduct :product="product" :horizontal="true" share/>
              </v-col>
            </v-row>
          </v-col>
        </v-row>
    </v-container>
  </v-main>
</template>

<script>
import MenuProduct from '@/components/MenuProduct'
import router from '../../../router'
// import { mapState } from 'vuex'

export default {
  name: 'WishList',
  components: {
    MenuProduct
  },
  data () {
    return {
      pagination: {
        limit: 12,
        offset: 0,
        page: 1,
        filter: null,
        order: '-name',
        total: 0
      },
      products: this.$store.getters.favoriteProducts(),
      pageProducts: []
    }
  },
  // computed: mapState(['favoriteProducts']),
  // watch: {
  //   favoriteProducts (newValue, oldValue) {
  //     console.log('old', oldValue)
  //     console.log('new', newValue)
  //     this.products = this.$store.getters.favoriteProducts()
  //   }
  // },
  methods: {
    addToCart (product) {
      this.$store.dispatch('addToCart', product)
      this.removeFromFavoriteProducts(product)
    },
    removeFromCart (product) {
      this.$store.dispatch('removeFromCart', product)
    },
    removeFromFavoriteProducts (product) {
      this.$store.dispatch('removeFromFavoriteProducts', product)
      this.products = this.$store.getters.favoriteProducts()
    },
    checkOnFavorites (product) {
      if (this.$store.getters.checkProductOnFavorites(this.product.id) === 0) {
        this.$store.dispatch('addToFavoriteProducts', product)
        product.isFavorite = true
      } else {
        this.$store.dispatch('removeFromFavoriteProducts', product)
        product.isFavorite = false
      }
    },
    getImgUrl (image) {
      var images = require.context('../../../assets/', false)
      return images('./' + image)
    },
    goToDetails (product) {
      router.push({ path: '/restaurant/' + this.product.section.toLowerCase() + '/' + this.product.id + '/details' })
      // router.push({ name: 'product-details', params: { product: product } })
    }
  }
}
</script>

<style lang="scss" scoped>
p{
  margin-bottom: 0;
}
.row{
  margin: 0;
}
.v-btn.success{
  background-color: #309999 !important;
  color: white !important;
  border-radius: 9px;
}

