import Vue from 'vue'
import VueRouter from 'vue-router'
import RestaurantHome from '@/views/restaurant-module/home'
import Restaurant from '../views/restaurant-module/restaurant'
import AcceptOrder from '../views/restaurant-module/order/accept-order.vue'
import PayOrder from '../views/restaurant-module/order/pay-order.vue'
import Settings from '../views/settings'
import Profile from '../views/settings/Profile.vue'
import Cards from '../views/settings/cards/Cards.vue'
import OperationsHistory from '../views/settings/OperationsHistory.vue'
import Destinataries from '../views/settings/Destinataries.vue'
import WishList from '../views/restaurant-module/wishes'
import Cart from '../views/restaurant-module/cart'
import RestaurantTabs from '../views/settings/restaurant/tabs.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/', redirect: 'home'
  },
  {
    path: '/home', name: 'home', component: RestaurantHome
  },
  {
    path: '/restaurant/:id', name: 'restaurant', component: Restaurant
  },
  {
    path: '/restaurant/:id/:product', name: 'product-details', component: Restaurant
  },
  {
    path: '/order/confirm', name: 'order-confirm', component: AcceptOrder
  },
  {
    path: '/order/pay', name: 'order-pay', component: PayOrder
  },
  {
    path: '/settings',
    component: Settings,
    children: [
      { path: '', component: Profile },
      { path: 'profile', name: 'profile', component: Profile },
      { path: 'restaurant', name: 'restaurant-tabs', component: RestaurantTabs },
      { path: 'cards', name: 'cards', component: Cards },
      { path: 'operations-history', name: 'operations-history', component: OperationsHistory },
      { path: 'contacts', name: 'destinataries', component: Destinataries }
    ]
  },
  {
    path: '/wish-list', name: 'wish-list', component: WishList
  },
  {
    path: '/cart', name: 'cart', component: Cart
  }

]

const scrollBehavior = (to, from, savedPosition) => {
  if (savedPosition) {
    return savedPosition
  }
  if (to.hash) {
    return { selector: to.hash }
  }
  return { x: 0, y: 0 }
}
const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
  scrollBehavior
})

export default router
