<template>
  <div>
    <v-data-table
      :headers="headers"
      :items="cards"
      item-key="name"
      class="elevation-0"
      :search="search"
      no-data-text="No hay tarjetas"
      no-results-text="Sin resultados"
    >
      <!-- Top row -->
      <template v-slot:top>
        <v-row align="center">
          <h2 class="text-bolder">TARJETAS DE CRÉDITO O DÉBITO</h2>
