          mdi-pencil
        </v-icon>
        <v-icon
          small
          @click="deleteItem(item)"
        >
          mdi-delete
        </v-icon>
      </template>

      <!-- <template v-slot:no-data>
        <v-btn
          color="primary"
          @click="initialize"
        >
          Reset
        </v-btn>
      </template> -->

    </v-data-table>

    <CardCreateDialog :dialog="dialog" :editedItem="editedItem" @save="save" @close="close" />
    <!-- Dialog for create action -->
    <!-- <v-dialog v-model="dialog" max-width="500px" scrollable>
      <v-card>
