                  label="Hacer predeterminada?"
                ></v-checkbox>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="blue darken-1"
            text
            @click="close"
          >
            Cancelar
          </v-btn>
          <v-btn
            color="blue darken-1"
            text
            @click="save"
          >
            Gaurdar
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog> -->

    <!-- Dialog for delete action -->
    <v-dialog v-model="dialogDelete" max-width="500px">
      <v-card>
        <v-card-title class="headline">Are you sure you want to delete this item?</v-card-title>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="closeDelete">Cancel</v-btn>
          <v-btn color="blue darken-1" text @click="deleteItemConfirm">OK</v-btn>
          <v-spacer></v-spacer>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import CardCreateDialog from './card-create-dialog/CardCreateDialog'
import { dateRules } from '@/mixins/date_rules'
import { numberRules } from '@/mixins/number_rules'

export default {
  name: 'Cards',
  components: {
    CardCreateDialog
  },
  mixins: [numberRules, dateRules],
  data () {
    return {
      search: '',
      number: '',
      dialog: false,
      dialogDelete: false,
      predeterminedCardId: false,
      editedIndex: -1,
      editedItem: {
        name: '',
        number: null,
        expiration_date: null,
        predetermined_card: false
      },
      defaultItem: {
        name: '',
        number: null,
        expiration_date: null,
        predetermined_card: false
      },
      cards: [
        {
          name: 'Frozen Yogurt',
          number: 159,
          expiration_date: 6.0,
          predetermined_card: false
        },
        {
          name: 'Ice cream sandwich',
          number: 237,
          expiration_date: 9.0,
          predetermined_card: false
        },
        {
          name: 'Eclair',
          number: 262,
          expiration_date: 16.0,
          predetermined_card: false
        },
        {
          name: 'Cupcake',
          number: 305,
          expiration_date: 3.7,
          predetermined_card: false
        }
      ]
    }
  },
  watch: {
    date (val) {
      this.editedItem.expiration_date = this.formatDate(this.date)
    },
    dialogDelete (val) {
      val || this.closeDelete()
    }
  },
  // created () {
  //   this.initialize()
  // },
  computed: {
    headers () {
      return [
        {
          text: 'Tarjeta predeterminada',
          align: 'center',
          sortable: false,
          value: 'predetermined_card'
        },
        {
          text: 'Nombre en la tarjeta',
          align: 'center',
          sortable: false,
          value: 'name'
        },
        {
          text: 'Número de la tarjeta',
          align: 'center',
          value: 'number'
          // filter: value => {
          //   if (!this.number) return true

          //   return value < parseInt(this.number)
          // }
        },
        { text: 'Fecha de expiración', align: 'center', value: 'expiration_date' },
        { text: '', align: 'center', value: 'actions', sortable: false }
      ]
    }
  },
  methods: {
    filterOnlyCapsText (value, search, item) {
      return value != null &&
          search != null &&
          typeof value === 'string' &&
          value.toString().toLocaleUpperCase().indexOf(search) !== -1
    },
    editItem (item) {
      this.editedIndex = this.cards.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialog = true
    },

    deleteItem (item) {
      this.editedIndex = this.cards.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialogDelete = true
    },

    deleteItemConfirm () {
      this.cards.splice(this.editedIndex, 1)
      this.closeDelete()
    },

    close () {
      this.dialog = false
      this.date = new Date().toISOString().substr(0, 10)
      this.menu = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },

    closeDelete () {
      this.dialogDelete = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },

    save (event) {
      if (this.editedIndex > -1) {
        Object.assign(this.cards[this.editedIndex], this.editedItem)
      } else {
        this.cards.push(this.editedItem)
      }
      this.close()
    }
  }
}
</script>

<style lang="scss" scoped>
.v-btn{
  font-size: 11px;
}
.row{
  margin: 18px 12px;
}
.theme--light.v-data-table > .v-data-table__wrapper > table > thead > tr:last-child > th:first-child {
  width: 50px !important;
  text-align: center;
}
</style>
